@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Nova+Mono&family=Courgette&display=swap);

.alt-text {
  color: rgba(0, 0, 0, 0.849);
}

.alt-title {
  color: rgba(0, 0, 0, 0.685);
  font-size: 5em;
  font-weight: bold;
  letter-spacing: 6px;
}

.alt-strong {
  color: #b82f1d;
}

.card {
  background-color: #ffffff10;
  box-shadow: 0 0 5px 3px #00000021 !important;
}

.data-title {
  font-family: 'Raleway', monospace;
  font-size: 1.5em;
  font-weight: bold;
  color: #3a0000a2;
}

.footer-text {
  padding: 0 50px;
  margin-top: 75px;
  font-size: 0.8em;
  color:rgba(0, 0, 0, 0.747);
}

.manifesto {
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
  font-family: 'Courgette', serif;
  cursor: pointer;
  color:rgba(0, 0, 0, 0.589);
  margin: 40px auto 10px;
}

.row {
  text-align: center;
}

.section {
  padding: 5px;
}

.section-final {
  background-color: rgba(255, 255, 255, 0.753);
  margin: 50px 0;
  padding: 15px;
  text-align: center;  
  box-shadow: 0 0 40px 60px rgba(255, 255, 255, 0.781);
}

.restiamo-a-casa {
  font-size: 1.4em;
  -webkit-animation-name: animaRestiamoACasa;
          animation-name: animaRestiamoACasa;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;  
  -webkit-animation-iteration-count: infinite;  
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; 
}

@-webkit-keyframes animaRestiamoACasa {
  0% {
    color: rgb(30, 150, 30);
  }
  30% {
    color: rgb(30, 150, 30);
  }
  100% {
    color: #b82f1d;
  }
}

@keyframes animaRestiamoACasa {
  0% {
    color: rgb(30, 150, 30);
  }
  30% {
    color: rgb(30, 150, 30);
  }
  100% {
    color: #b82f1d;
  }
}
body {
  background-color: #ffcccc;
  background: linear-gradient(to top, #ffb4b4 -4%, #c4ff8d 120%);
  height: auto;
  background-repeat: no-repeat;
  height: 100%;
}

a {
  color:rgba(0, 0, 0, 0.5);
  font-weight: bold;
}

a:hover {  
  text-decoration: none;
}

a:hover {
  color: #b82f1dda;
}
a:active {
  color:  #5c8408; 
}

h1 {
  font-family: 'Raleway', monospace;
  font-size: 2.5em;
  font-weight: bold;
  color: #3a000033;
  position: absolute;
  top: 0;
  right: 15px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

h2 {
  font-family: 'Raleway', monospace;
}

h3 {
  margin: 15px 0;
  font-size: 1.8em;
  font-weight: bold;
  font-family: 'Raleway', monospace;
  color: #b82f1d;
  text-align: center;  
  letter-spacing: 1px;
}

h6 {
  text-align: right;
  font-size: 4em;
  margin: 5px 0;
  font-family: 'Raleway', monospace;
  font-weight: bold;
  color: #3a0000a2;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

hr {
  width: 100px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.158);
}

p {
  text-align: center;
  font-family: 'Raleway', sans-serif;
}

strong {  
  letter-spacing: 1px;
  color: #741003;
}
